          @click="this.isCorrection = !this.isCorrection"
        >
          Ï∑®ÏÜå
        </button>
        <textarea
          style="
            border: 3px solid black;
            width: 80%;
            height: 70px;
            display: block;
            font-size: 50px;
            font-weight: bold;
            padding-top: 12px;
            background: none;
            border: none;
            outline: none;
            text-align: left;
            overflow: hidden;
            margin-top: -75px;
            margin-left: 120px; /* ÏàòÏ†ï: ÏôºÏ™Ω Ïó¨Î∞± Ï°∞Ï†à */
            width: calc(100% - 150px); /* ÏàòÏ†ï: ÎÑàÎπÑ Ï°∞Ï†à */
          "
          v-model="correctionInformations.title"
        >
        </textarea>
      </div>
      <a v-else class="titleContext">{{ this.contents[fullDate].title }}</a>
    </div>
    <div
      class="contentBox"
      v-if="fullDate in this.contents"
      :style="{ background: this.contents[fullDate].color }"
      :class="{ correctionContentBox: this.isCorrection }"
    >
      <div v-if="this.isCorrection">
        <textarea
          style="
            width: 99%;
            height: 200px;
            display: block;
            font-size: 30px;
            padding-left: 25px;
            padding-top: 10px;
            background: none;
            border: none;
            outline: none;
            text-align: left;
            overflow: hidden;
          "
          v-model="correctionInformations.context"
        ></textarea>
      </div>
      <div v-else>
        <button class="delete" @click="deleteContent(this.fullDate)">X</button>
        <button
          class="correction"
          @click="this.isCorrection = !this.isCorrection"
        >
          V
        </button>
        <a class="context">{{ this.contents[fullDate].context }}</a>
      </div>
    </div>
    <a class="title" v-else>Ïò§ÎäòÏùò Í∏∞Î∂ÑÏùÑ ÏïåÎ†§Ï£ºÏÑ∏Ïöî!</a>
    <div class="calendar">
      <div class="header">
        <button class="calenderArrow" @click="prevMonth">&lt;</button>
        <span>{{ currentMonth }}</span>
        <button class="calenderArrow" @click="nextMonth">&gt;</button>
      </div>
      <div class="days">
        <div
          v-for="day in days"
          :key="day"
          class="day"
          @click="handleDateClick(day)"
          :class="{ selected: selectedDay === day }"
        >
          {{ day }}
          <br />
          <div v-for="writedDay in this.writedMoods" :key="writedDay">
            <a
              style="font-size: 30px"
              v-if="
                writedDay.date ==
                this.selectedDateExceptDay + day.toString().padStart(2, '0')
              "
              >{{ this.moodTable[writedDay.mood] }}</a
            >
          </div>
        </div>
      </div>
    </div>
