</template>

<script>
import router from "@/router/router";
export default {
  data() {
    return {
      correctionInformations: {
        title: "",
        context: "",
        date: "",
        color: "",
        mood: "",
      },
      isCorrection: false,
      currentDate: new Date(),
      selectedDate: new Date().getDate(), // ÏÑ†ÌÉùÎêú ÎÇ†ÏßúÎ•º Ï†ÄÏû•Ìï† Î≥ÄÏàò
      selectedDay: new Date().getDate(),
      info: {},
      moods: {},
      contents: {},
      writedMoods: [],
      monthNames: [
        "01",
        "02",
        "03",
        "04",
        "05",
        "06",
        "07",
        "08",
        "09",
        "10",
        "11",
        "12",
      ],
      moodTable: {
        1: "üò°",
        2: "ü§Æ",
        3: "üò®",
        4: "üò´",
        5: "üòî",
        6: "üòÆ‚Äçüí®",
        7: "üò™",
        8: "üòê",
        9: "üòÅ",
        10: "ü§£",
        11: "ü•π",
        12: "üòä",
        13: "üòé",
        14: "üò≠",
        15: "üòÇ",
        16: "üòç",
      },
    };
  },
  mounted() {
    this.$store.commit("saveDate");
    this.$axios
      .post("/list")
      .then((res) => {
        this.info = res.data;
        for (var i = 0; i < this.info.length; i++) {
          this.moods = {};
          this.moods["mood"] = this.info[i].mood;
          this.moods["date"] = this.info[i].date;
          this.contents[this.info[i].date] = {
            context: this.info[i].context,
            color: this.info[i].color,
            title: this.info[i].title,
            mood: this.info[i].moods,
          };

          this.writedMoods.push(this.moods);
        }
      })
      .catch(function (error) {
        alert(error);
      });
  },
  computed: {
    currentMonth() {
      return (
        this.currentDate.getFullYear() +
        "ÎÖÑ  " +
        this.monthNames[this.currentDate.getMonth()] +
        "Ïõî"
      );
    },
    days() {
      const daysInMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        0
      ).getDate();
      return Array.from({ length: daysInMonth }, (_, i) => i + 1);
    },
    fullDate() {
      return (
        this.currentDate.getFullYear().toString() +
        "-" +
        this.monthNames[this.currentDate.getMonth()] +
        "-" +
        this.selectedDay.toString().padStart(2, "0")
      );
    },
    selectedDateExceptDay() {
      return (
        this.currentDate.getFullYear().toString() +
        "-" +
        this.monthNames[this.currentDate.getMonth()] +
        "-"
      );
    },
  },

  methods: {
    correctContent() {
      this.correctionInformations.date = this.fullDate;
      this.correctionInformations.color = this.contents[this.fullDate].color;

      for (var i = 0; i < this.writedMoods.length; i++) {
        if (this.fullDate == this.writedMoods[i].date) {
          this.correctionInformations.mood = this.writedMoods[i].mood;
        }
      }
      var info = {
        date: "",
        mood: "",
      };
      info["date"] = this.fullDate;

      for (i = 0; i < this.writedMoods.length; i++) {
        if (this.writedMoods[i].date == this.correctionInformations.mood) {
          info["mood"] = this.writedMoods[i].mood;
          this.writedMoods[i] = info;
        }
      }

      this.$axios
        .post("/correction", this.correctionInformations)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    deleteContent(date) {
      this.$axios
        .post("/delete", date)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    loadContents() {},
    prevMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() - 1,
        1
      );
    },
    nextMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        1
      );
    },
    handleDateClick(day) {
      this.selectedDay = day;
      this.$store.commit("saveDate", this.fullDate);
    },
  },
};
</script>

