<template>
  <div>
    <div class="inBoardCalendarDiv">
      <span
        style="
          font-size: 30px;
          margin-left: 20px;
          width: 200px; /* ÏõêÌïòÎäî ÎÑàÎπÑÎ°ú Ï°∞Ï†ï */
        "
        >{{ currentMonth }}</span
      >
      <button class="calenderArrow" style="margin-left: 5px" @click="prevMonth">
        &lt;
      </button>
      <div class="inBoardDaysDiv">
        <div v-for="day in days" :key="day" class="inBoardDayContainer">
          <div v-for="row in this.savedContents" :key="row">
            <div
              class="inBoardWritedDiv"
              v-if="
                selectedDateExceptDay + day.toString().padStart(2, '0') ==
                row.date
              "
            ></div>
          </div>

          <button
            class="inBoardDayButton"
            @click="handleDateClick(day)"
            :class="{ selectedInBoardDay: selectedDay === day }"
          >
            {{ day }}
          </button>
        </div>
      </div>
      <button class="calenderArrow" @click="nextMonth">&gt;</button>
    </div>
    <div style="border-bottom: 3px solid black; height: 10px; margin-top: 10%">
      <br />

      <button
        style="
          font-size: 30px;
          font-weight: bold;
          border: none;
          background: white;
          text-align: center;
          float: right;
        "
        @click="[createNewForm(), getColor()]"
        v-if="!isWrited(fullDate)"
      >
        +
      </button>
    </div>
    <form>
      <div
        class="titleFormOuterBox"
        :class="{ isFormOpen: formBox }"
        :style="{ background: color }"
        v-if="isFormOpen"
      >
        <button class="submitButton" @click="saveContents" type="submit">
          ÏûëÏÑ±
        </button>
        <textarea
          style="
            border: 3px solid black;
            width: 80%;
            height: 70px;
            display: block;
            font-size: 50px;
            font-weight: bold;

            background: none;
            border: none;
            outline: none;
            text-align: left;
            overflow: hidden;
            margin-top: -50px;
            margin-left: 120px; /* ÏàòÏ†ï: ÏôºÏ™Ω Ïó¨Î∞± Ï°∞Ï†à */
            width: calc(100% - 150px); /* ÏàòÏ†ï: ÎÑàÎπÑ Ï°∞Ï†à */
          "
          v-model="title"
        >
        </textarea>
      </div>

      <div
        class="formOuterBox"
        :class="{ isFormOpen: formBox }"
        :style="{ background: color }"
        v-if="isFormOpen"
      >
        <textarea
          style="
            width: 99%;
            height: 200px;
            display: block;
            font-size: 30px;
            padding-left: 25px;
            padding-top: 10px;
            background: none;
            border: none;
            outline: none;
            text-align: left;
            overflow: hidden;
          "
          v-model="context"
        ></textarea>
      </div>
    </form>
    <div v-for="(row, idx) in this.savedContents" :key="idx">
      <div v-if="this.savedContents[idx].date == fullDate">
        <br />
        <div
          class="titleBox"
          :style="{ background: this.savedContents[idx].color }"
        >
          <a class="titleContext">{{ this.savedContents[idx].title }}</a>
        </div>

        <div
          :key="(idx, componentKey)"
          class="box"
          :style="{ background: this.savedContents[idx].color }"
        >
          <button class="delete" @click="deleteContent(this.fullDate)">
            X
          </button>
          <button
