</template>

<script>
import router from "@/router/router";
export default {
  name: "contentBox",
  data() {
    return {
      monthNames: [
        "01",
        "02",
        "03",
        "04",
        "05",
        "06",
        "07",
        "08",
        "09",
        "10",
        "11",
        "12",
      ],
      currentDate: new Date(),
      selectedDate: new Date().getDate(),
      selectedDay: new Date().getDate(),
      color: 0,
      isFormOpen: false,
      contents: {
        title: "",
        context: "",
        color: "",
        mood: this.$store.state.todaysMood.contents.mood,
        date: "",
      },
      moodTable: {
        1: "üò°",
        2: "ü§Æ",
        3: "üò®",
        4: "üò´",
        5: "üòî",
        6: "üòÆ‚Äçüí®",
        7: "üò™",
        8: "üòê",
        9: "üòÅ",
        10: "ü§£",
        11: "ü•π",
        12: "üòä",
        13: "üòé",
        14: "üò≠",
        15: "üòÇ",
        16: "üòç",
      },
      title: "",
      context: "",
      savedContents: [],
      componentKey: 0,
      savedContentsDates: [],
    };
  },

  components: {},
  mounted() {
    this.loadContents();
    this.currentDate = new Date(this.$store.state.todaysMood.contents.date);
    this.selectedDate = this.currentDate.getDate();
    this.selectedDay = this.currentDate.getDate();
  },

  methods: {
    isWrited(date) {
      for (var i = 0; i < this.savedContentsDates.length; i++) {
        if (date == this.savedContentsDates[i]) {
          return true;
        }
      }
      return false;
    },
    loadDates() {
      for (var i = 0; i < this.savedContents.length; i++) {
        this.savedContentsDates.push(this.savedContents[i].date);
      }
    },
    prevMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() - 1,
        1
      );
    },
    nextMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        1
      );
    },
    handleDateClick(day) {
      this.selectedDay = day;
    },
    getColor() {
      const colors = `hsl(${parseInt(Math.random() * 24, 10) * 15},62%, 59%)`;
      this.color = colors;
    },
    createNewForm() {
      this.context = "";
      this.isFormOpen = true;
    },

    saveContents() {
      this.contents.color = this.color;
      this.contents.context = this.context;
      this.contents.mood = this.$store.state.todaysMood.contents.mood;
      this.contents.date = this.fullDate;
      this.contents.title = this.title;
      this.isFormOpen = false;

      this.$axios
        .post("/save", this.contents)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    loadContents() {
      this.$axios
        .post("/list")
        .then((res) => {
          this.savedContents = res.data;
          this.loadDates();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    init() {
      this.$store.commit("initToDoList");
    },
  },
  computed: {
    currentMonth() {
      return (
        this.currentDate.getFullYear() +
        "ÎÖÑ  " +
        this.monthNames[this.currentDate.getMonth()] +
        "Ïõî"
      );
    },
    days() {
      const daysInMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        0
      ).getDate();
      return Array.from({ length: daysInMonth }, (_, i) => i + 1);
    },
    fullDate() {
      return (
        this.currentDate.getFullYear().toString() +
        "-" +
        this.monthNames[this.currentDate.getMonth()] +
        "-" +
        this.selectedDay.toString().padStart(2, "0")
      );
    },
    selectedDateExceptDay() {
      return (
        this.currentDate.getFullYear().toString() +
        "-" +
        this.monthNames[this.currentDate.getMonth()] +
        "-"
      );
    },
  },
};
</script>

